.login-container {
  width: 100vw;
  height: 100vh;
  background-color: @dark-white-color;
  .login-header {
    // background-color: @primary-color;
    // width: 100%;
    // height: 350px;
  }

  .login-form-container {
    width: 500px;
    height: 300px;
    display: block;
    position: fixed;
    top: 0;
    bottom: 100px;
    left: 0;
    right: 0;
    margin: auto;
    background-color: @white-color;
    padding: 24px !important;
    // box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  }

  .logo-container {
    align-items: center;
    padding: 16px;
    width: 30%;
    margin: auto;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .login-google {
    text-align: center;

    .btn-container {
      align-items: center;
      padding: 24px 48px;
    }
  }
}

